import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedstate from 'vuex-persistedstate'
Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [createPersistedstate({
    reducer: (state) => {
      return {
        // 默认是全部缓存，在这里可以设置需要缓存的状态
        deviceId: state.deviceId,
        closeShop: state.closeShop,
        salesGoodsSearchList: state.salesGoodsSearchList
      }
    }
  })],
  state: {
    // baseUrl: 'https://www.yphflower.com', // 正式
    baseUrl: 'https://test.yphflower.com', // 测试
    deviceId: '', // 设备id
    closeShop: '', // 是否开关店
    isShowTabBar: false, // 是否显示tabbar
    salesGoodsSearchList: []// 在售商品搜索历史记录
  },
  getters: {
  },
  mutations: {
    setDeviceId (state, deviceId) {
      state.deviceId = deviceId
    },
    updateCloseShop (state, closeShop) {
      state.closeShop = closeShop
    },
    updateShowTabBar (state, isShowTabBar) {
      state.isShowTabBar = isShowTabBar
    },
    setSalesGoodsSearchList (state, salesGoodsSearchList) {
      state.salesGoodsSearchList = salesGoodsSearchList
    }

  },
  actions: {
  },
  modules: {
  }
})
