import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
import index from './modules/index'
import news from './modules/news'
import goods from './modules/goods'
import order from './modules/order'
import my from './modules/my'
import login from './modules/login'
import common from './modules/common'

Vue.use(VueRouter)

const routes = [
  ...index,
  ...news,
  ...goods,
  ...order,
  ...my,
  ...login,
  ...common,
  {
    path: '*',
    redirect: '/index'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.afterEach((to, from) => {
  // const keepAlive = to.meta.keepAlive
  if (to.path === '/index' || to.path === '/news' || to.path === '/goods' || to.path === '/order' || to.path === '/my') {
    if (store.state.isShowTabBar === false) {
      store.commit('updateShowTabBar', true)
    }
  } else {
    if (store.state.isShowTabBar === true) {
      store.commit('updateShowTabBar', false)
    }
  }
})

