<template>
  <div>
    <transition :name="transitionName">
      <keep-alive :include="includeAlive">
        <router-view></router-view>
      </keep-alive>
    </transition>
    <tabbar v-show="$store.state.isShowTabBar"></tabbar>
  </div>
</template>

<script>

import tabbar from '@/components/TabBar.vue'
import VueRouter from 'vue-router'

export default {
  data () {
    return {
      transitionName: '',
      csonstAlive: ['index', 'news', 'goods', 'order', 'my'],
      includeAlive: ['index', 'news', 'goods', 'order', 'my']
    }
  },
  components: {
    tabbar
  },
  created () {

  },
  mounted () {
    const that = this
    const originalReplace = VueRouter.prototype.replace
    VueRouter.prototype.replace = function push (location, onResolve, onReject) {
      // 将末尾的进行移除
      that.includeAlive.splice(that.includeAlive.length - 1, 1)
      if (onResolve || onReject) {
        return originalReplace.call(this, location, onResolve, onReject)
      }
      return originalReplace.call(this, location).catch(err => err)
    }
  },
  watch: {

    // 使用watch 监听$router的变化
    $route (to, from) {
      // console.log(to, from)
      // 如果to索引大于from索引, 判断为前进状态, 反之则为后退状态
      const toIndex = to.meta.index
      const fromIndex = from.meta.index
      const toAlive = to.meta.keepAlive
      const toName = to.name
      const fromName = from.name
      const toIndexResult = this.csonstAlive.indexOf(toName) === -1
      const fromIndexResult = this.csonstAlive.indexOf(fromName) === -1
      let level
      if (fromIndex === undefined || (!toIndexResult && !fromIndexResult)) {
        this.transitionName = 'onefade'
        level = 0
        if (fromIndex === undefined && toAlive) {
          level = 1
        }
      } else if (toIndex >= fromIndex) {
        this.transitionName = 'slide-left'
        level = 1
      } else {
        this.transitionName = 'slide-right'
        level = 2
      }
      // 判断当前页是否需要缓存
      if (level === 1 && toAlive && toIndexResult) {
        // 判断是否存在
        if (this.includeAlive.indexOf(toName) === -1) this.includeAlive.push(toName)
        // console.log('添加==>', toName, this.includeAlive)
      }
      const fromAlive = from.meta.keepAlive

      if (level === 2 && fromAlive && fromName) {
        // 要删除当前缓存页面
        const rmoveIndex = this.includeAlive.indexOf(fromName)
        if (rmoveIndex > -1) this.includeAlive.splice(rmoveIndex, 1)
        // console.log('删除==>', fromName, this.includeAlive)
      }
    }
  }
}
</script>

