<template>
  <div ref="wrapper" class="wrapper">
    <div>
      <div v-if="refresher" class="scroll-head">
        <van-loading v-show="isShowLoad" type="spinner" size="18px"></van-loading>
        <div class="loading-text">{{ loadContent }}</div>
      </div>
      <slot></slot>
      <div class="no-data" v-if="noMoreData">{{ finishedText }}</div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">

import BScroll from 'better-scroll'
import { Loading } from 'vant'

/**
 * @scrolltolower 上拉加载更多触发此方法
 * @pullrefresh 下拉刷新
 */
export default {
  components: {
    [Loading.name]: Loading
  },
  data () {
    return {
      loadContent: '下拉刷新',
      isShowLoad: false
    }
  },
  props: {
    downSetting: {
      type: Object,
      default: () => {
        return {
          threshold: 80,
          stop: 60
        }
      }
    },
    upThreshold: {
      type: Number,
      default: 200
    },
    /**
       * 是否开启下拉刷新
       */
    refresher: {
      type: Boolean,
      default: false
    },
    /**
       * 是否开启上拉加载更多
       */
    loadmore: {
      type: Boolean,
      default: false
    },
    // 是否还有数据 true则不触发上拉事件
    noMoreData: {
      type: Boolean,
      default: false
    },
    // 没有数据时提示文字内容
    finishedText: {
      type: String,
      default: '没有更多了'
    },
    /**
       * 是否开启回弹效果
       */
    bounce: {
      type: [Boolean, Object],
      default: true
    },
    /**
       * 是否开启滚动条
       */
    scrollbar: {
      type: Boolean,
      default: false
    },
    /**
       * 1 滚动的时候会派发scroll事件，会截流。
       * 2 滚动的时候实时派发scroll事件，不会截流。
       * 3 除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
       */
    probeType: {
      type: Number,
      default: 1
    },
    /**
       * 点击列表是否派发click事件
       */
    click: {
      type: Boolean,
      default: true
    },
    /**
       * 是否开启横向滚动
       */
    scrollX: {
      type: Boolean,
      default: false
    },
    /**
       * 是否派发滚动事件
       */
    listenScroll: {
      type: Boolean,
      default: false
    },
    /**
       * 列表的数据
       */
    data: {
      type: [Object, Array],
      default: null
    },
    /**
       * 是否派发滚动到底部的事件，用于上拉加载
       */
    pullup: {
      type: Boolean,
      default: false
    },
    /**
       * 是否派发顶部下拉的事件，用于下拉刷新
       */
    pulldown: {
      type: Boolean,
      default: false
    },
    /**
       * 是否派发列表滚动开始的事件
       */
    beforeScroll: {
      type: Boolean,
      default: false
    },
    /**
       * 当数据更新后，刷新scroll的延时。
       */
    refreshDelay: {
      type: Number,
      default: 20
    }
  },
  mounted () {
    // 保证在DOM渲染完毕后初始化better-scroll
    setTimeout(() => {
      this._initScroll()
    }, 20)
  },
  methods: {
    // 下拉刷新
    onRefresh () {
      if (!this.refresher) {
        return
      }
      this.$emit('pullrefresh')
    },
    refreshed () {
      this.scroll.finishPullDown()
    },
    loaded () {
      this.scroll.finishPullUp()
    },
    _initScroll () {
      if (!this.$refs.wrapper) {
        return
      }
      // better-scroll的初始化
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: this.click,
        scrollX: this.scrollX,
        mouseWheel: true,
        scrollbar: this.scrollbar,
        bounce: this.bounce,
        pullUpLoad: this.loadmore ? { threshold: this.upThreshold } : this.loadmore,
        pullDownRefresh: this.refresher ? this.downSetting : this.refresher
      })
      // 是否派发滚动事件
      if (this.listenScroll) {
        this.scroll.on('scroll', (pos) => {
          this.$emit('scroll', pos)
        })
      }

      // 是否派发上拉加载事件
      if (this.loadmore) {
        this.scroll.on('pullingUp', () => {
          if (this.noMoreData) {
            return
          }
          this.$emit('scrolltolower')
        })
      }

      // 是否派发下拉刷新事件
      if (this.refresher) {
        this.scroll.on('pullingDown', () => {
          this.$emit('pullrefresh')
        })
        const threshold = this.downSetting.threshold
        this.scroll.on('scroll', (pos) => {
          if (pos.y > threshold && !this.isShowLoad) {
            this.loadContent = '释放立即刷新'
          } else if (!this.isShowLoad) {
            this.loadContent = '下拉刷新'
          }
          if (pos.y < 30) {
            this.loadContent = '下拉刷新'
            this.isShowLoad = false
          }
        })
        this.scroll.on('touchEnd', (pos) => {
          if (pos.y > threshold) {
            this.loadContent = '加载中...'
            this.isShowLoad = true
          }
        })
      }

      // 是否派发列表滚动开始的事件
      if (this.beforeScroll) {
        this.scroll.on('beforeScrollStart', () => {
          this.$emit('beforeScroll')
        })
      }
    },
    disable () {
      // 代理better-scroll的disable方法
      this.scroll && this.scroll.disable()
    },
    enable () {
      // 代理better-scroll的enable方法
      this.scroll && this.scroll.enable()
    },
    refresh () {
      // 代理better-scroll的refresh方法
      this.scroll && this.scroll.refresh()
    },
    scrollTo () {
      // 代理better-scroll的scrollTo方法
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
    scrollToElement () {
      // 代理better-scroll的scrollToElement方法
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    }
  },
  watch: {
    // 监听数据的变化，延时refreshDelay时间后调用refresh方法重新计算，保证滚动效果正常
    data () {
      setTimeout(() => {
        this.refresh()
      }, this.refreshDelay)
    }
  }
}
</script>
<style lang="scss" scoped>
.wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.scroll-head {
  margin-top: -50px;
  position: absolute;
  height: 30px;
  width: 100%;
  line-height: 30px;
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.loading-text {
  color: #999999;
  margin-left: 8px;
  font-size: 16px;
}

.no-data {
  font-size: .875rem;
  color: #D3D3D3;
  padding: 1rem;
  text-align: center;
}
</style>
