const goods = [
  {
    path: '/goods',
    name: 'goods',
    component: () => import('@/views/goodsTab/Goods'),
    meta: { index: 0, keepAlive: true }
  },
  {
    path: '/updatePriceStock',
    name: 'updatePriceStock',
    component: () => import('@/views/goodsTab/UpdatePriceStock'),
    meta: { index: 2 }
  },
  {
    path: '/goodsSearch',
    name: 'goodsSearch',
    component: () => import('@/views/goodsTab/GoodsSearch'),
    meta: { index: 1, keepAlive: true }
  },
  {
    path: '/editAttribute',
    name: 'editAttribute',
    component: () => import('@/views/goodsTab/EditAttribute'),
    meta: { index: 2 }
  }
]

export default goods
