const news = [
  {
    path: '/news',
    name: 'news',
    component: () => import('@/views/newsTab/News'),
    meta: { index: 0, keepAlive: true }
  },
  {
    path: '/newsTypeList',
    name: 'newsTypeList',
    component: () => import('@/views/newsTab/NewsTypeList'),
    meta: { index: 1 }
  }
]

export default news
