const order = [
  {
    path: '/order',
    name: 'order',
    component: () => import('@/views/orderTab/Order'),
    meta: { index: 0, keepAlive: true }
  },
  {
    path: '/orderScreen',
    name: 'orderScreen',
    component: () => import('@/views/orderTab/OrderScreen'),
    meta: { index: 1 }
  }
]

export default order
